import React, { useState } from "react";
import { useEffect } from "react";
import { Button, Modal, Row, Table, Form } from "react-bootstrap";
import { toast } from "react-toastify";


function DocTypeModal(props) {

  useEffect(() => {
    if (props.editDocumentType!=null && props.editDocumentType[2]) {
      document.getElementById("text").value = props.editDocumentType[0].text;
      document.getElementById("value").value = props.editDocumentType[0].value;
    }
  }, [props.editDocumentType]);

  const handleSubmit = async (event) => {
    event.preventDefault();

    const value = event.target.value.value;
    const text = event.target.text.value;
    let _data = [...props.allDocumentTypeList];
    let json = { "text": text, "value": value };
    if (props.editDocumentType[2])
      _data[props.editDocumentType[1]] = json;
    else
      _data.push(json);
    props.setAllDocumentTypeList(_data);
    await props.updateWebsiteDataCall(_data);
    props.onhide();
  }

  return (
    <Modal show={props.modalshow} onHide={props.onhide} className="popup-lg">
      <Modal.Body style={{ padding: '0px' }}>
        <div className="popup-box">
          <div className="closed">
            <a style={{ border: 'none' }} onClick={props.onhide}><span style={{ fontSize: '25px' }}></span></a>
          </div>

          <div className="popup-data">
            <div className="popup-header">
              <h3>{props.editDocumentType!=null?(props.editDocumentType[2]?"Edit Data":"Add Data"):""}</h3>
            </div>
            <div className="popup-body">
              <Row>
                <Form onSubmit={handleSubmit}>
                  <Form.Group className="mb-3" controlId="value">
                    <Form.Label for="value">Value of document Type.(Camelcase string without spaces.)</Form.Label>
                    <Form.Control className="input-design" type="text" id="value" placeholder="value" required />
                  </Form.Group>
                  <Form.Group className="mb-3" controlId="text">
                    <Form.Label for="text">Text of document Type.(User will see this text in dropdown)</Form.Label>
                    <Form.Control className="input-design" type="text" id="text" placeholder="Text" required />
                  </Form.Group>
                  <Row>
                    <Button variant="primary" type="submit" disabled={props.loading}>
                      Confirm
                    </Button>
                  </Row>
                </Form>
              </Row>
            </div>
            {/* <div className="footer-popup">
              <p>Note: Password should be correct or else your data will not be saved.</p>
            </div> */}
          </div>

        </div>
      </Modal.Body>
    </Modal>
  );
}

export default DocTypeModal;