import axios from 'axios';

const serverurl = "https://kyc-project-api.herokuapp.com/api";

//"http://localhost:8080/api";
// role :-
// admin->1
// customer->2
// merchant->3

export const createCustomer = async (email, password, walletAddress) => {
    var response = await axios({
        method: 'post',
        url: serverurl + '/Users/create-user',
        data: {
            "name": email,
            "email": email,
            "password": password,
            "role": "2",
            "walletAddress": walletAddress
        }, 
        headers: {
            'Content-Type': 'application/json'
        },
    });
    return response.data;
}

export const createMerchant = async (email, password, walletAddress) => {
    var response = await axios({
        method: 'post',
        url: serverurl + '/Users/create-user',
        data: {
            "name": email,
            "email": email,
            "password": password,
            "role": "3",
            "walletAddress": walletAddress
        }, 
        headers: {
            'Content-Type': 'application/json'
        },
    });
    return response.data;
}

export const userLogin = async (email, password) => {
    var response = await axios({
        method: 'post',
        url: serverurl + '/Users/login-user',
        data: {
            "email": email,
            "password": password
        }, 
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        },
    });
    return response.data;
}

