export const createDocument = async (formData, _accessToken) => {
    var response = await axios({
        method: 'post',
        url: serverurl + '/Documents/create-document?access_token=' + _accessToken,
        data: formData, 
        headers: {
            'content-type': 'multipart/form-data',
            'Accept': 'application/json'
        },
    });
    return response.data;
}

export const getMyDocumentsDocument = async (_accessToken) => {
    var response = await axios({
        method: 'get',
        url: serverurl + '/Documents/get-document-by-auth?access_token=' + _accessToken,
    });
    return response.data;
}

export const deactivateDocument = async (docId, _accessToken) => {
    var response = await axios({
        method: 'put',
        url: serverurl + '/Documents/document-active-deactive?access_token=' + _accessToken,
        data: {
            "id": docId,
            "status": "deactive"
        }, 
        headers: {
            'Accept': 'application/json'
        },
    });
    return response.data;
}

export const getDocument = async (url) => {
    let resultData="data:application/pdf;base64,";
    var response = await fetch('http://' + url,{headers: {'Accept': "application/text"},})
        .then((res) => {
            if (!res.ok) {
                return res.status.toString()
            }
            return res.arrayBuffer()
        })
        .then((data) => {
            var base64Str = Buffer.from(data).toString('base64');
            // console.log(Buffer.base64Decode(base64Str, 'application/pdf'));
            console.log(base64Str);
            resultData+=base64Str;
        })
        .catch(
            (err) => {
                return err.Message;
            });
    return resultData;
}

