export const updateWebsiteData = async ( _noOfDocumentAllowed, _documentTypeList, _accessToken) => {
    var response = await axios({
        method: 'put',
        url: serverurl + '/Websites/update-website?access_token=' + _accessToken,
        data: {
            "noOfDocumentAllowed":_noOfDocumentAllowed,
            "documentTypeList": _documentTypeList,
            "id": "633c108dc92054001626bd99"
        }, 
        headers: {
            'Accept': 'application/json'
        },
    });
    return response.data;
}