import axios from 'axios';

export const getAdharVerificationToken = async () => {
    var response = await axios({
        method: 'post',
        url: 'https://kyc-api.surepass.io/api/v1/users/username-login',
        data: {
            "username": "jewria",
            "password": "Jewria@12",
            "role": ["user"]
        }, 
        headers: {
            'Content-Type': 'application/json'
        },
    });
    return response.data.data.access_token;
}

export const generateAdharVerificationOTP = async (adharid, token) => {
    var response = await axios({
        method: 'post',
        url: 'https://kyc-api.surepass.io/api/v1/aadhaar-v2/generate-otp',
        data: {
            "id_number": adharid
        }, 
        headers: {
            'Authorization': 'Bearer '+token,
            'access-control-allow-origin': 'https://dash.surepass.io',
            'access-control-allow-credentials': true,
            'Content-Type': 'application/json'
        },
    });
    return response.data.data.client_id;
}

export const submitAdharVerificationOTP = async (otp, clientId, token) => {
    var response = await axios({
        method: 'post',
        url: 'https://kyc-api.surepass.io/api/v1/aadhaar-v2/submit-otp',
        data: {
            "otp": otp,
            "client_id": clientId
        },
        headers: {
            'Authorization': 'Bearer '+token,
            'Content-Type': 'application/json'
        },
    });
    return response.data.data;
}