import React, { useContext, useEffect, useState } from "react";
import { Navbar, Nav, Container, Offcanvas, NavDropdown, Form, FormControl, Button, Image } from 'react-bootstrap';

import { useLocation, useHistory } from "react-router-dom";
import { LoginContext } from "../../pages/LoginContext";

const Menubar = (props) => {

    const [loginData, setLoginData] = useContext(LoginContext);

    const history = useHistory()
    const location = useLocation()

    useEffect(() => {
        // console.log(location.pathname);
        props.updateBalance();
    }, [loginData]);

    const getWalletAdddressTrim = (val) => {
        return val.substr(0, 5) + "...." + val.slice(-4);
    }

    return (
        <>
            <Navbar collapseOnSelect bg="dark" variant="dark" expand="lg">
                <Container>
                    <Navbar.Brand href="/">
                        KYC Authentication
                    </Navbar.Brand>

                    {loginData.username != "" ?
                        <>
                            <Navbar.Toggle aria-controls="basic-navbar-nav" />
                            <Navbar.Collapse id="basic-navbar-nav" className="justify-content-end">
                                <Nav>
                                    <Nav.Link onClick={() => props.updateBalance()}><span>INRT Tokens: </span>{props.tokenCount}
                                    </Nav.Link>
                                    <NavDropdown title={getWalletAdddressTrim(loginData.walletAddress)}>
                                        <NavDropdown.Item href="#" onClick={() => {
                                                setLoginData({
                                                    walletAddress: "",
                                                    isConnected: false,
                                                    username: "",
                                                    web3: null,
                                                    role: null,
                                                    KYCContract: null,
                                                    INRTContract: null
                                                });
                                                history.push('/');
                                            }}>Logout
                                        </NavDropdown.Item>
                                    </NavDropdown>
                                </Nav>
                            </Navbar.Collapse>
                        </>
                        : <></>}
                </Container>
            </Navbar>
        </>
    )
}
export default Menubar