import React, { useState } from "react";
import { Button, Modal, Row, Table, Form } from "react-bootstrap";
import { toast } from "react-toastify";


function ConfirmPasswordModal(props) {

  const handleSubmit = async (event) => {
    event.preventDefault();

    const password = event.target.password.value;
    props.saveallobjectdata(password);
    props.onhide();
  }

  return (
    <Modal show={props.modalshow} onHide={props.onhide} className="popup-lg">
      <Modal.Body style={{ padding: '0px' }}>
        <div className="popup-box">
          <div className="closed">
            <a style={{ border: 'none' }} onClick={props.onhide}><span style={{ fontSize: '25px' }}></span></a>
          </div>

          <div className="popup-data">
            <div className="popup-header">
              <h3>Confirm</h3>
            </div>
            <div className="popup-body">
              <Row>
                <Form onSubmit={handleSubmit}>
                  <Form.Group className="mb-3" controlId="password">
                    <Form.Label for="password">Enter your account password to confirm.</Form.Label>
                    <Form.Control className="input-design" type="password" id="password" placeholder="Password" required />
                  </Form.Group>
                  <Row>
                  <Button variant="primary" type="submit" disabled={props.loading}>
                    Confirm
                  </Button>
                  </Row>
                </Form>
              </Row>
            </div>
            <div className="footer-popup">
              <p>Note: Password should be correct or else your data will not be saved.</p>
            </div>
          </div>

        </div>
      </Modal.Body>
    </Modal>
  );
}

export default ConfirmPasswordModal