import React, { useContext, useEffect, useState } from "react";
import { Row, Col, Container, Button, Table, Dropdown, DropdownButton, Form, Tooltip } from 'react-bootstrap';
import { toast } from "react-toastify";
import { useLocation, useHistory } from "react-router-dom";
import { LoginContext } from "../LoginContext";

import ConfirmPasswordModal from "../../components/page-components/confirmPasswordModal";
import { getAdharVerificationToken, generateAdharVerificationOTP, submitAdharVerificationOTP } from '../../components/api/AdharVerificationCalls';
import { createDocument, getMyDocumentsDocument, deactivateDocument, getWebsiteData } from '../../components/api/ProjectCalls';
import { encrypt, decrypt } from '../../components/Encryption/file';

const AddNewRecord = (props) => {

    const [selectedDocumentTypeText, setSelectedDocumentTypeText] = useState(false);
    const [fileDataBuffer, setFileDataBuffer] = useState(null);
    const [loginData, setLoginData] = useContext(LoginContext);
    // Show no access model
    const [confirmPasswordmodalShow, setConfirmPasswordModalShow] = useState(false);
    // verification is completed for adhar
    const [adharVerification, setAdharVerification] = useState(-1);//-1 not started, 0 for started, 1 for completed

    const [authToken, setAuthToken] = useState("");
    const [otpVerificationClient, setOTPVerificationClient] = useState("");
    const [documentTypeList, setDocumentTypeList] = useState([]);


    useEffect(() => {
        if (adharVerification == -1) {
            document.getElementById("nameOnDocument").value = "";
            document.getElementById("documentId").value = "";
            document.getElementById("documentFile").value = "";
        }
        getWebsiteVariables();
    }, [adharVerification]);


    const getWebsiteVariables = async () => {
        let res = await getWebsiteData();
        setDocumentTypeList(res.data[0].documentTypeList);
    }

    //to update document type selected
    //whether it adhar, pan, etc
    const documentTypeUpadated = (e) => {
        // console.log(e.target.value);
        if (e.target.value == "other")
            setSelectedDocumentTypeText(true);
        else if (selectedDocumentTypeText)
            setSelectedDocumentTypeText(false);
    }

    //convert image to base64 string
    function handleFileSelect(evt) {
        // console.log(evt);
        if (evt.target.files[0]) {
            evt.preventDefault();
            // console.log(URL.createObjectURL(evt.target.files[0]));

            const reader = new FileReader();
            reader.readAsArrayBuffer(evt.target.files[0])
            reader.addEventListener("load", () => {
                setFileDataBuffer(reader.result)
            })
        }
    }

    const handleSubmit = async (event) => {
        event.preventDefault();
        props.setLoading(true);
        if (props.myAllDocumentCount > 1) {
            props.setLoading(false);
            toast.error("Only 2 documents are free to upload. Please purchase INRT tokens to add more documents.");
            return;
        }
        // console.log(event)
        // check for document already exists
        const alreadyExist = props.documentsData.some(element => {
            if (element.documentType === (event.target.documentType.value == "other" ? event.target.documentTypeText.value : event.target.documentType.value)) {
                return true;
            }
            return false;
        });
        if (alreadyExist) {
            props.setLoading(false);
            console.log("Please select unique document type");
            toast.error("Please select unique document type");
            return;
        }
        let adharFullName = event.target.nameOnDocument.value;
        let verifiedData = "";
        // checks for adhar verification
        if (event.target.documentType.value == "adhar") {
            if (adharVerification == -1) {
                //call for verification code
                try {
                    let _authToken = await getAdharVerificationToken();
                    // console.log(_authToken);
                    setAuthToken(_authToken);
                    let _otpVerificationClient = await generateAdharVerificationOTP(event.target.documentId.value, _authToken);
                    // console.log(_otpVerificationClient);
                    setOTPVerificationClient(_otpVerificationClient);
                    setAdharVerification(0);
                    props.setLoading(false);
                    return;
                } catch (error) {
                    console.error(error);
                    if (error.response.data.data.valid_aadhaar)
                        toast.error("Adhar document id is invalid");
                    else if (error.response.data.data.otp_sent)
                        toast.error("Error while sending OTP");
                    else
                        toast.error("Error while adhar verification");
                    props.setLoading(false);
                    return;
                }
            } else if (adharVerification == 0) {
                //call for verify otp
                try {
                    let response = await submitAdharVerificationOTP(event.target.otp.value, otpVerificationClient, authToken);
                    console.log(response);
                    adharFullName = response.full_name;
                    let res = {
                        "aadhaarNumber": response.aadhaar_number,
                        "fullName": response.full_name,
                        "gender": response.gender,
                        "hasImage": response.has_image,
                        "careOf": response.care_of,
                        "address": response.address,
                        "zip": response.zip
                    };
                    verifiedData = JSON.stringify(res);
                } catch (error) {
                    console.error(error);
                    toast.error("OTP verification failed");
                    props.setLoading(false);
                    return;
                }
            }
        }

        var formData = {
            "nameOnDocument": adharFullName,
            "documentId": event.target.documentId.value,
            "documentType": event.target.documentType.value == "other" ? event.target.documentTypeText.value : event.target.documentType.value,
            "document": event.target.documentFile.files[0],
            "status": "active",
            "isVerified": adharVerification == 0 ? true : false,
            "verifiedDocumentDetails": verifiedData
        }
        console.log(formData, loginData.accessToken);
        let res = await createDocument(formData, loginData.accessToken);
        console.log(res);
        if (res.success) {
            let res = await getMyDocumentsDocument(loginData.accessToken);
            console.log(res);
            if (res.success) {
                let arr = res.data.filter(function (ele) { return ele.status == "active" });
                props.setDocumentsData(arr);
            }
            // var _allDocumentsData = [...props.documentsData];
            // _allDocumentsData.push(res.data);
            // props.setDocumentsData(_allDocumentsData);
            // console.log(_allDocumentsData);
            //reset verification
            setAdharVerification(-1);
            setOTPVerificationClient("");
            setAuthToken("");
        } else {
            toast.error("Error while saving document data");
        }
        props.setLoading(false);
    }

    const saveAllObjectData = async (password) => {
        props.setLoading(true);
        let encryptedCid = encrypt(props.documentsData[0].user_id);
        // blockchain call
        try {
            var txId = await loginData.KYCContract.methods.updateCustomerData(loginData.username, encryptedCid, password).send({ from: loginData.walletAddress });
            console.log(txId);
            if (txId) {
                toast.success("All data updated!");
            } else {
                toast.error("Error while updateing Data");
                console.error("Error while  updateing Data");
            }
        } catch (err) {
            var text = err.message.substr(err.message.indexOf('"message": "') + 12);
            var message = text.substr(0, text.indexOf('"'));
            toast.error(message);
            console.log(err.message);
            // console.log(err);
        }
        props.setLoading(false);
    }

    const deleteObject = async (key, id) => {
        props.setLoading(true);
        let res = await deactivateDocument(id, loginData.accessToken);
        console.log(res);
        if (res.success) {
            toast.success("Document deleted sucessfully");
        } else
            toast.error("Error while deleting document");
        // var arr = [...props.documentsData];
        // arr.splice(key, 1);
        // props.setDocumentsData(arr);
        //update my documents
        res = await getMyDocumentsDocument(loginData.accessToken);
        console.log(res);
        if (res.success) {
            props.setMyAllDocumentCount(res.data.length);
            let arr = res.data.filter(function (ele) { return ele.status == "active" });
            props.setDocumentsData(arr);
        }
        props.setLoading(false);
    }

    return (<>
        <>
            <ConfirmPasswordModal
                modalshow={confirmPasswordmodalShow}
                onhide={() => setConfirmPasswordModalShow(false)}
                saveallobjectdata={saveAllObjectData}
            />
        </>
        <Row>
            <div className="page-heading">
                <h2> Add New Record</h2>
                {/* <span> Add New Record</span> */}
            </div>
        </Row>
        <Row style={{ padding: "10px" }}>
            <label className="text-align-center color-red">(Click on this button to save all changes)</label>
            <Button onClick={() => setConfirmPasswordModalShow(true)}>Update All Data</Button>
        </Row>
        {props.documentsData.length > 0 ?
            <div className="add-table">
                <Table striped bordered hover variant="light">
                    <thead>
                        <tr>
                            <th>#</th>
                            <th>Document Type</th>
                            <th>Name on Document</th>
                            <th>Document ID</th>
                            {/* <th>Address</th> */}
                            <th>File Url</th>
                            <th>Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {props.documentsData.map((data, key) => {
                            return (
                                <tr key={key}>
                                    <td>{key + 1}</td>
                                    <td>{data?.documentType}{data?.isVerified == "true" ? <i class="fa fa-check-circle" aria-hidden="true" style={{ color: "blue" }}></i> : <></>}</td>
                                    <td style={{ wordBreak: "break-all" }}>{data?.nameOnDocument}</td>
                                    <td>{data?.documentId}</td>
                                    {/* <td>{data.address}</td> */}
                                    <td style={{ wordBreak: "break-all" }}>{data?.fileUrl}</td>
                                    <td>
                                        <Button variant="danger" onClick={() => deleteObject(key, data.id)}><i className="fa fa-trash" aria-hidden="true"></i></Button>
                                    </td>
                                </tr>
                            );
                        })}
                    </tbody>
                </Table>
            </div>
            : <></>}

        <div className="title-heading">
            <h4>Add Document</h4>
        </div>

        {/* Format documents */}
        <div className="add-new-access-form">

            <Form className="deal-form" onSubmit={handleSubmit}>

                <Row>
                    <Col md="6">
                        <Form.Group className="mb-3" controlId="formDocumentType">
                            <Form.Label controlId="documentType">Select Document type</Form.Label><span className="color-red">*</span>
                            <br /><Form.Text className="text-muted">Must be unique for each document</Form.Text>
                        </Form.Group>
                    </Col>
                    <Col md="6">
                        <Form.Group className="mb-3" controlId="formDocumentType">
                            <Form.Select id="documentType" onChange={documentTypeUpadated} disabled={adharVerification == 0} required>
                                <option value=''>-</option>
                                {documentTypeList.map((data, key) => {
                                    return (
                                        <option key={key} value={data.value}>{data.text}</option>
                                    );
                                })}
                            </Form.Select>
                            {selectedDocumentTypeText ?
                                <Form.Control type="text" id="documentTypeText" className="input-design" placeholder="" required /> :
                                <></>
                            }
                        </Form.Group>
                    </Col>
                </Row>

                <Row>
                    <Col md="6">
                        <Form.Group className="mb-3" controlId="formNameOnDocument">
                            <Form.Label controlId="nameOnDocument">Your Name</Form.Label><span className="color-red">*</span><br />
                            <Form.Text className="text-muted" controlId="nameOnDocument">As per written on document</Form.Text>
                        </Form.Group>
                    </Col>
                    <Col md="6">
                        <Form.Group className="mb-3" controlId="formNameOnDocument">
                            <Form.Control type="text" id="nameOnDocument" className="input-design" placeholder="" disabled={adharVerification == 0} required />
                        </Form.Group>
                    </Col>
                </Row>
                <Row>
                    <Col md="6">
                        <Form.Group className="mb-3" controlId="formDocumentId">
                            <Form.Label controlId="documentId">Document ID</Form.Label><span className="color-red">*</span>
                        </Form.Group>
                    </Col>
                    <Col md="6">
                        <Form.Group className="mb-3" controlId="formDocumentId">
                            <Form.Control type="text" id="documentId" className="input-design" placeholder="" disabled={adharVerification == 0} required />
                        </Form.Group>
                    </Col>
                </Row>
                <br />
                <Row>
                    <Col md={9} className="offset-3">
                        <Form.Group className="mb-3  input-form" controlId="formDocumentFile">
                            <Form.Control type="file" id="documentFile" className="input-design"
                                onChange={handleFileSelect}
                                accept=".pdf"
                                // image/*,.pdf
                                placeholder="" disabled={adharVerification == 0} required />
                        </Form.Group>
                    </Col>
                </Row>

                <br />
                {adharVerification == 0 ?
                    <Row>
                        <Col md="6">
                            <Form.Group className="mb-3" controlId="fromOTP">
                                <Form.Label>OTP</Form.Label><br />
                                <Form.Text className="text-muted">Check your registered Document</Form.Text>
                            </Form.Group>
                        </Col>

                        <Col md="6">
                            <Form.Group className="mb-3" controlId="fromOTP">
                                <Form.Control type="text" id="otp" className="input-design" placeholder="" required />
                            </Form.Group>
                        </Col>
                    </Row>
                    : <></>}
                <br />
                <Row>
                    <Col md={9} className="offset-3">
                        <Form.Group controlId="formSubmit">
                            <Button variant="dark" type="submit">SUBMIT</Button>
                        </Form.Group>
                    </Col>
                </Row>
            </Form>
            <br /><br /><br />
        </div>

    </>)
}

export default AddNewRecord;