import React, { useContext, useEffect, useState } from "react";
import { Row, Col, Container, Button, Badge, Tab, Tabs, Table, Form, Tooltip } from 'react-bootstrap';
import { toast } from "react-toastify";
import '../../components/css/style.css';
import { address } from '../../components/contracts/ContractData';
import MerchantLogo from '../../components/images/merchant.png';

import { getUserDataByUserId, getDocument, getUserDocTypeList } from '../../components/api/ProjectCalls';
import PdfViewer from "../../components/page-components/PdfViewer";
import { useLocation, Redirect, useHistory } from "react-router-dom";
import { LoginContext } from "../LoginContext";
import { encrypt, decrypt } from '../../components/Encryption/file';

const MerchantHomePage = (props) => {

    const history = useHistory();
    const [isDocumentDownloadable, setIsDocumentDownloadable] = useState(false);
    const [showDocumentURL, setShowDocumentURL] = useState("");
    const [documentAccessData, setDocumentAccessData] = useState({});
    const [documentsData, setDocumentsData] = useState([]);
    const [selectedNavigation, setSelectedNavigation] = useState("dashboard");
    const [loginData, setLoginData] = useContext(LoginContext);
    // Dashbord counts
    const [allPendingRequestCount, setAllPendingRequestCount] = useState(0);
    const [allAccessAllowedCount, setAllAccessAllowedCount] = useState(0);
    const [myReferralTokenCount, setMyReferralTokenCount] = useState(0);
    // All reuests
    const [allAccessRequestSelectedTab, setAllAccessRequestSelectedTab] = useState("requested")
    const [allRequestData, setAllRequestData] = useState([]);
    //add new request
    const [userDocumentTypeList, setUserDocumentTypeList] = useState([]);

    //All access allowed users  
    const [allowedAccessUserList, setAllowedAccessUserList] = useState([]);

    useEffect(() => {
        getAllRequests();
        getMyReferralRewardCount();
    }, []);

    const getMyReferralRewardCount = async () => {
        var result = await loginData.KYCContract.methods.merchantReferralRewardCount(loginData.username).call({ from: loginData.walletAddress });
        // console.log(result);
        setMyReferralTokenCount(result);
    }

    //claim Referral Tokens
    const claimReferralTokens = async () => {
        props.setLoading(true);
        if (myReferralTokenCount > 0) {
            try {
                var tx = await loginData.KYCContract.methods.claimMerchantReferralReward(loginData.username).send({ from: loginData.walletAddress });
                console.log(tx);
                if (tx) {
                    toast.success("Referral Reward Claimed");
                    await getMyReferralRewardCount();
                    //update my balance
                    var result = await loginData.INRTContract.methods.balanceOf(loginData.walletAddress).call();
                    console.log(result);
                    if (props.tokenCount != result)
                        props.setTokenCount(result / 10 ** 6);
                }
            } catch (err) {
                toast.error("Error while claiming reward");
                console.log(err.message);
            }
        } else
            toast.error("You don't have any referrals");
        props.setLoading(false);
    }

    const getAllRequests = async () => {
        props.setLoading(true);
        var length = await loginData.KYCContract.methods.getAllMyMerchantsRequestsLength(loginData.username).call({ from: loginData.walletAddress });
        // console.log(length);
        var allRequests = [];
        var allowedAccessUsers = [];
        var pendingRequestCount = 0;
        var accessAllowedCount = 0;
        for (let i = 0; i < length; i++) {
            var CustomerName = await loginData.KYCContract.methods.getAllMyMerchantsRequestsData(loginData.username, i).call({ from: loginData.walletAddress });
            console.log(CustomerName);
            var result = await loginData.KYCContract.methods.getAllRequestsData(loginData.username, CustomerName).call({ from: loginData.walletAddress });
            console.log(result);
            if (result.status == 1)
                pendingRequestCount++;
            else if (result.status == 2) {
                if (parseInt(new Date().getTime() / 1000) < result.closeTime)
                    accessAllowedCount++;
                allowedAccessUsers.push(CustomerName);
            }
            var obj = {
                "customerName": CustomerName,
                "info": result.info,
                "status": result.status,
                "timestamp": result.timestamp,
                "openTime": result.openTime,
                "closeTime": result.closeTime
            }
            allRequests.push(obj);
        }
        setAllPendingRequestCount(pendingRequestCount);
        setAllAccessAllowedCount(accessAllowedCount);
        setAllRequestData(allRequests);
        setAllowedAccessUserList(Array.from(new Set(allowedAccessUsers)));
        props.setLoading(false);
    }

    // get allowed user data
    const getUserData = async (event) => {
        event.preventDefault();
        props.setLoading(true);
        // console.log(event.target.name.value)
        var formData = {
            "name": event.target.name.value
        }
        console.log(formData);
        if (loginData.connect == false) {
            toast.error("Please connect metamask first");
            return;
        }
        try {
            var tx = await loginData.KYCContract.methods.checkRequestStatus(loginData.username, formData.name).send({ from: loginData.walletAddress });
            console.log(tx);
            if (tx.events.RequestStatusChanged != null) {
                getAllRequests();
            }
            var result = await loginData.KYCContract.methods.getUserData(loginData.username, formData.name).call({ from: loginData.walletAddress });
            // console.log(result);
            // console.log(decrypt(result[0]),decrypt(result[1]));
            //byte32 hash of data
            if (result) {
                let res = await getUserDataByUserId(decrypt(result[0]))
                setDocumentsData(res.data);
                let documentAccessData = decrypt(result[1]);
                // console.log(documentAccessData);
                setDocumentAccessData(JSON.parse(documentAccessData));
                toast.success("Data fetched successfully!");
            } else {
                toast.error("Error while fetching Request");
                console.error("Error while fetching Request");
            }
        } catch (err) {
            var text = err.message.substr(err.message.indexOf('"message": "') + 12);
            var message = text.substr(0, text.indexOf('"'));
            toast.error(message);
            console.log(err.message);
        }
        props.setLoading(false);
    }

    // set details to show document
    const showDocument = async (data) => {
        // let decryptedUrl = decrypt(data.fileUrlHash);
        // console.log(decryptedUrl);
        // setShowDocumentURL(decryptedUrl);
        let url = await getDocument(data);
        setShowDocumentURL(url);
        // set view or downloadable
        if (documentAccessData[data.documentType] == "View")
            setIsDocumentDownloadable(false);
        else
            setIsDocumentDownloadable(true);


    }

    const getUserDocumentTypeList = async (username) => {
        // console.log(username);
        let res = await getUserDocTypeList(username);
        // console.log(res);
        if (res.success) {
            setUserDocumentTypeList(res.data);
            console.log(res.data);
        }
    }

    function isEmpty(obj) {
        return Object.keys(obj).length === 0;
    }

    //send access requets
    const handleSubmit = async (event) => {
        event.preventDefault();
        props.setLoading(true);
        // console.log(event.target.name.value)
        
        var jsonData = {};
        userDocumentTypeList.forEach(function (element, index) {
        var isSelected = document.getElementById("select" + index).checked;
        if (isSelected) {
            if (document.getElementById("view" + index).checked) {
            let _accessType = document.getElementById("view" + index).value;
            jsonData[element] = _accessType;
            } else if (document.getElementById("download" + index).checked) {
            let _accessType = document.getElementById("download" + index).value;
            jsonData[element] = _accessType;
            }
        }
        });
        console.log(jsonData);
        if (isEmpty(jsonData)) {
            toast.error("Please select documents first");
            props.setLoading(false);
            return;
        }

        var formData = {
            "name": event.target.name.value,
            "info": event.target.info.value,
            "documentsAllowed": JSON.stringify(jsonData),
        }
        console.log(formData);
        if (loginData.connect == false) {
            props.setLoading(false);
            toast.error("Please connect metamask first");
            return;
        }
        try {
            var allowance = await loginData.INRTContract.methods.allowance(loginData.walletAddress, address).call();
            if (allowance < 1000000) {
                var tx = await loginData.INRTContract.methods.approve(address, 1000000).send({ from: loginData.walletAddress });
            }
            var txId = await loginData.KYCContract.methods.sendAccessRequest(loginData.username, formData.name, formData.documentsAllowed, formData.info).send({ from: loginData.walletAddress });
            console.log(txId);
            if (txId) {
                getAllRequests();
                toast.success("Request send successfully!");
            } else {
                toast.error("Error while sending Request");
                console.error("Error while sending Request");
            }
        } catch (err) {
            var text = err.message.substr(err.message.indexOf('"message": "') + 12);
            var message = text.substr(0, text.indexOf('"'));
            toast.error(message);
            console.log(err.message);
        }

        props.setLoading(false);
    }

    return (
        loginData.isConnected ? <>
            <input type="checkbox" id="side" name="" value="" />
            <div className="main-data-row  page-wrap" >
                <div className="side-bar-menu sidebar">
                    <div className="homepage-navigation">
                        <div className="menu-user">
                            <div className="user-img"><img src={MerchantLogo} /></div>
                            <p>Merchant</p>
                        </div>
                        <ul>
                            <li><Button variant="secondary" size="lg" className={selectedNavigation == "dashboard" ? "active" : ""} onClick={() => setSelectedNavigation("dashboard")}><i className="fa fa-tachometer" aria-hidden="true"></i> Dashboard</Button></li>
                            <li><Button variant="secondary" size="lg" className={selectedNavigation == "viewuserdata" ? "active" : ""} onClick={() => setSelectedNavigation("viewuserdata")}> <i className="fa fa-user" aria-hidden="true"></i> View User Data</Button></li>
                            <li><Button variant="secondary" size="lg" className={selectedNavigation == "allaccessrequest" ? "active" : ""} onClick={() => setSelectedNavigation("allaccessrequest")}><i className="fa fa-key" aria-hidden="true"></i> All Access Request</Button></li>
                            <li><Button variant="secondary" size="lg" className={selectedNavigation == "createnewaccessrequest" ? "active" : ""} onClick={() => {setSelectedNavigation("createnewaccessrequest");setUserDocumentTypeList([]);}}><i className="fa fa-pencil-square-o" aria-hidden="true"></i> Create New Access Request</Button></li>
                            <li><Button variant="secondary" size="lg" className={selectedNavigation == "referralReward" ? "active" : ""} onClick={() => setSelectedNavigation("referralReward")}><i className="fa fa-gift" aria-hidden="true"></i> Referral Rewards</Button></li>
                        </ul>
                    </div>
                </div> {/* sidebar closed */}

                <div className="dashboard-content page-content">
                    <label for="side" className="toggle"></label>
                    <Row className="homepage-pageData">
                        {/* dashboard page */}
                        {selectedNavigation == "dashboard" ?
                            <>
                                <div className="page-heading">
                                    <h2> Welcome to KYC!</h2>
                                    <span> An KYC authentication using blockchain.</span>
                                    <br /><br />
                                </div>

                                <Row className="dashboard-summary-details">
                                    <Col md={4}>
                                        <Button variant="primary" size="lg" onClick={() => { setSelectedNavigation("allaccessrequest"); setAllAccessRequestSelectedTab("approved"); }}>
                                            Access Records Allowed <Badge bg="secondary">{allAccessAllowedCount}</Badge>
                                            <span className="visually-hidden">Access Records Allowed {allAccessAllowedCount}</span>
                                        </Button>
                                    </Col>
                                    <Col md={4}>
                                        <Button variant="primary" size="lg" onClick={() => { setSelectedNavigation("allaccessrequest"); setAllAccessRequestSelectedTab("requested"); }}>
                                            Pending Requests <Badge bg="secondary">{allPendingRequestCount}</Badge>
                                            <span className="visually-hidden">Pending Requests {allPendingRequestCount}</span>
                                        </Button>
                                    </Col>
                                    <Col md={4}>
                                        <Button variant="primary" size="lg" onClick={() => { setSelectedNavigation("referralReward"); }}>
                                            Referral Token Count <Badge bg="secondary">{myReferralTokenCount}</Badge>
                                            <span className="visually-hidden">Referral Token Count {myReferralTokenCount}</span>
                                        </Button>
                                    </Col>
                                </Row>
                            </> :
                            <></>}

                        {/*  viewuserdata page */}
                        {selectedNavigation == "viewuserdata" ?
                            <>
                                <div className="page-heading">
                                    <h2> View User Data</h2>
                                    {/* <span> All Allowed Access Records </span> */}
                                    <br /><br />
                                </div>

                                {/* Form to select user */}
                                <div className="user-form-box">

                                    <Form className="deal-form" onSubmit={getUserData}>

                                        <Row>
                                            <Col md="6">
                                                <Form.Group className="mb-3" controlId="name">
                                                    <Form.Label>Username</Form.Label><br />
                                                    <Form.Text className="text-muted">As per used on this platform</Form.Text>
                                                </Form.Group>
                                            </Col>
                                            <Col md="6">
                                                <Form.Group className="mb-3" controlId="name">
                                                    <Form.Select name="username" id="name">
                                                        {allowedAccessUserList.map((data, key) => {
                                                            return (
                                                                <option value={data}>{data}</option>
                                                            );
                                                        })}
                                                    </Form.Select>
                                                    {/* <Form.Control type="text" id="name" className="input-design" placeholder="" required /> */}
                                                </Form.Group>
                                            </Col>
                                        </Row>
                                        <br />
                                        <Row>
                                            <Col className="submit-btn text-align-center">
                                                <Button variant="dark" type="submit">SUBMIT</Button>
                                            </Col>
                                        </Row>
                                    </Form>
                                </div>

                                <Row style={{ marginTop: "20px" }}>
                                    {documentsData.length > 0 ?
                                        <Table striped bordered hover variant="light">
                                            <thead>
                                                <tr>
                                                    <th>#</th>
                                                    <th>Document Type</th>
                                                    <th>Name on Document</th>
                                                    <th>Document ID</th>
                                                    <th>Access Type</th>
                                                    <th>File Url Hash</th>
                                                    <th>Actions</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {documentsData.map((data, key) => {
                                                    return (
                                                        <tr key={key}>
                                                            <td>{key + 1}</td>
                                                            <td>{data.documentType}</td>
                                                            <td style={{ wordBreak: "break-all" }}>{data.nameOnDocument}</td>
                                                            <td>{data.documentId}</td>
                                                            <td>{documentAccessData[data.documentType]}</td>
                                                            <td style={{ wordBreak: "break-all" }}>{encrypt(data.fileUrl)}</td>
                                                            <td>
                                                                <Button onClick={() => showDocument(data.fileUrl)}>Show</Button>
                                                            </td>
                                                        </tr>
                                                    );
                                                })}
                                            </tbody>
                                        </Table>
                                        : <></>}
                                </Row>
                                {showDocumentURL || showDocumentURL != "" ?
                                    <Row>
                                        {isDocumentDownloadable ?
                                            <>
                                                <Button variant="dark" onClick={() => setShowDocumentURL("")}>Close View</Button>
                                                <embed src={showDocumentURL} width="100%" height="500px" />
                                            </>
                                            :
                                            <PdfViewer pdf={showDocumentURL}
                                                setShowDocumentURL={setShowDocumentURL} />
                                        }
                                    </Row>
                                    : <></>
                                }
                            </> :
                            <></>}
                        {/*  allaccessrequest page */}
                        {selectedNavigation == "allaccessrequest" ?
                            <>
                                <div className="page-heading">
                                    <h2> All Access Requests</h2>
                                    {/* <span> All Access Requests.</span> */}
                                </div>

                                <Tabs id="AllAccessRequestTab" className="mb-3 tabs-dashboard"
                                    activeKey={allAccessRequestSelectedTab}
                                    onSelect={(k) => setAllAccessRequestSelectedTab(k)} >
                                    <Tab eventKey="requested" title="Requested">
                                        <Row>
                                            <Table striped bordered hover variant="light">
                                                <thead>
                                                    <tr>
                                                        <th>#</th>
                                                        <th>Merchant Name</th>
                                                        <th>Info</th>
                                                        <th>Status</th>
                                                        <th>Request Send Timestamp</th>
                                                        <th>Access Open Timestamp</th>
                                                        <th>Access Close Timestamp</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {allRequestData.map((data, key) => {
                                                        return (
                                                            data.status == 1 ?
                                                                <tr>
                                                                    <td>{key + 1}</td>
                                                                    <td>{data.customerName}</td>
                                                                    <td>{data.info}</td>
                                                                    <td>Requested</td>
                                                                    <td>{new Date(parseInt(data.timestamp * 1000)).toLocaleString()}</td>
                                                                    <td>{data.openTime == 0 ? "-" : new Date(parseInt(data.openTime * 1000)).toLocaleString()}</td>
                                                                    <td>{data.closeTime == 0 ? "-" : new Date(parseInt(data.closeTime * 1000)).toLocaleString()}</td>
                                                                </tr>
                                                                : <></>
                                                        );
                                                    })}
                                                </tbody>
                                            </Table>
                                        </Row>
                                    </Tab>
                                    <Tab eventKey="approved" title="Approved">
                                        <Row>
                                            <Table striped bordered hover variant="light">
                                                <thead>
                                                    <tr>
                                                        <th>#</th>
                                                        <th>Merchant Name</th>
                                                        <th>Info</th>
                                                        <th>Status</th>
                                                        <th>Request Send Timestamp</th>
                                                        <th>Access Open Timestamp</th>
                                                        <th>Access Close Timestamp</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {allRequestData.map((data, key) => {
                                                        return (
                                                            data.status == 2 && parseInt(new Date().getTime() / 1000) < data.closeTime ?
                                                                <tr>
                                                                    <td>{key + 1}</td>
                                                                    <td>{data.customerName}</td>
                                                                    <td>{data.info}</td>
                                                                    <td>Approved</td>
                                                                    <td>{new Date(parseInt(data.timestamp * 1000)).toLocaleString()}</td>
                                                                    <td>{data.openTime == 0 ? "-" : new Date(parseInt(data.openTime * 1000)).toLocaleString()}</td>
                                                                    <td>{data.closeTime == 0 ? "-" : new Date(parseInt(data.closeTime * 1000)).toLocaleString()}</td>
                                                                </tr>
                                                                : <></>
                                                        );
                                                    })}
                                                </tbody>
                                            </Table>

                                        </Row>
                                    </Tab>
                                    <Tab eventKey="rejected" title="Rejected">
                                        <Row>
                                            <Table striped bordered hover variant="light">
                                                <thead>
                                                    <tr>
                                                        <th>#</th>
                                                        <th>Merchant Name</th>
                                                        <th>Info</th>
                                                        <th>Status</th>
                                                        <th>Request Send Timestamp</th>
                                                        <th>Access Open Timestamp</th>
                                                        <th>Access Close Timestamp</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {allRequestData.map((data, key) => {
                                                        return (
                                                            data.status == -1 ?
                                                                <tr>
                                                                    <td>{key + 1}</td>
                                                                    <td>{data.customerName}</td>
                                                                    <td>{data.info}</td>
                                                                    <td>Rejected</td>
                                                                    <td>{new Date(parseInt(data.closeTime * 1000)).toLocaleString()}</td>
                                                                    <td>{data.openTime == 0 ? "-" : new Date(parseInt(data.openTime * 1000)).toLocaleString()}</td>
                                                                    <td>{data.closeTime == 0 ? "-" : new Date(parseInt(data.timestamp * 1000)).toLocaleString()}</td>
                                                                </tr>
                                                                : <></>
                                                        );
                                                    })}
                                                </tbody>
                                            </Table>

                                        </Row>
                                    </Tab>
                                    <Tab eventKey="closed" title="Closed">
                                        <Row>
                                            <Table striped bordered hover variant="light">
                                                <thead>
                                                    <tr>
                                                        <th>#</th>
                                                        <th>Merchant Name</th>
                                                        <th>Info</th>
                                                        <th>Status</th>
                                                        <th>Request Send Timestamp</th>
                                                        <th>Access Open Timestamp</th>
                                                        <th>Access Close Timestamp</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {allRequestData.map((data, key) => {
                                                        return (
                                                            data.status == 3 || parseInt(new Date().getTime() / 1000) > data.closeTime ?
                                                                <tr>
                                                                    <td>{key + 1}</td>
                                                                    <td>{data.customerName}</td>
                                                                    <td>{data.info}</td>
                                                                    <td>Closed</td>
                                                                    <td>{new Date(parseInt(data.timestamp * 1000)).toLocaleString()}</td>
                                                                    <td>{data.openTime == 0 ? "-" : new Date(parseInt(data.openTime * 1000)).toLocaleString()}</td>
                                                                    <td>{data.closeTime == 0 ? "-" : new Date(parseInt(data.closeTime * 1000)).toLocaleString()}</td>
                                                                </tr>
                                                                : <></>
                                                        );
                                                    })}
                                                </tbody>
                                            </Table>

                                        </Row>
                                    </Tab>
                                    <Tab eventKey="all" title="All">
                                        <Row>
                                            <Table striped bordered hover variant="light">
                                                <thead>
                                                    <tr>
                                                        <th>#</th>
                                                        <th>Merchant Name</th>
                                                        <th>Info</th>
                                                        <th>Status</th>
                                                        <th>Request Send Timestamp</th>
                                                        <th>Access Open Timestamp</th>
                                                        <th>Access Close Timestamp</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {allRequestData.map((data, key) => {
                                                        return (
                                                            <tr>
                                                                <td>{key + 1}</td>
                                                                <td>{data.customerName}</td>
                                                                <td>{data.info}</td>
                                                                <td>{data.status == 1 ? "Requested" :
                                                                    data.status == 2 ? "Approved" :
                                                                        data.status == -1 ? "Rejected" : "Closed"}</td>
                                                                <td>{new Date(parseInt(data.timestamp * 1000)).toLocaleString()}</td>
                                                                <td>{data.openTime == 0 ? "-" : new Date(parseInt(data.openTime * 1000)).toLocaleString()}</td>
                                                                <td>{data.closeTime == 0 ? "-" : new Date(parseInt(data.closeTime * 1000)).toLocaleString()}</td>
                                                            </tr>
                                                        );
                                                    })}
                                                </tbody>
                                            </Table>

                                        </Row>
                                    </Tab>
                                </Tabs>

                            </> :
                            <></>}
                        {/*  createnewaccessrequest page */}
                        {selectedNavigation == "createnewaccessrequest" ?
                            <>
                                <div className="page-heading">
                                    <h2> Create New Access Request</h2>
                                    <span> Note: Make sure the username is correct</span>
                                </div>

                                {/* Format documents */}
                                <div className="user-form-box">

                                    <Form className="deal-form" onSubmit={handleSubmit}>

                                        <Row>
                                            <Col md="6">
                                                <Form.Group className="mb-3" controlId="name">
                                                    <Form.Label>Username</Form.Label><span className="color-red">*</span>
                                                    <Form.Text className="text-muted"> (As per used on this platform) </Form.Text>
                                                    <Form.Control type="text" id="name" className="input-design" placeholder="Enter username" onChange={(e) => getUserDocumentTypeList(e.target.value)} required />
                                                </Form.Group>
                                            </Col>
                                            <Col md="6">
                                                <Form.Group className="mb-3" controlId="info">
                                                    <Form.Label>Info</Form.Label>
                                                    <Form.Text className="text-muted"> (max 50 characters) </Form.Text>
                                                    <Form.Control type="text" id="info" className="input-design" placeholder="optional info" required maxLength="50" />
                                                </Form.Group>
                                            </Col>
                                        </Row>
                                        {userDocumentTypeList.length>0?
                                        <Row>
                                            <p>Select documents with proper access type<span className="color-red">*</span></p>
                                            <Table striped bordered hover variant="light">
                                                <thead>
                                                    <tr>
                                                        <th>#</th>
                                                        <th>Document Type</th>
                                                        <th>Select</th>
                                                        <th>Access type</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {userDocumentTypeList.map((element, key) => {
                                                        return (
                                                            <tr key={key}>
                                                                <td>{key + 1}</td>
                                                                <td>{element}</td>
                                                                <td className="text-align-center">
                                                                    <input type="checkbox" id={"select" + key} name="selected" value="select" />
                                                                </td>
                                                                <td>
                                                                    <label><input type="radio" id={"view" + key} name={"accessType" + key} value="View" defaultChecked />
                                                                        View</label> &ensp;
                                                                    <label><input type="radio" id={"download" + key} name={"accessType" + key} value="Download" />
                                                                        Download</label>
                                                                </td>
                                                            </tr>
                                                        );
                                                    })}
                                                </tbody>
                                            </Table>

                                        </Row>
                                        :<></>}
                                        <br />
                                        <Row>
                                            <Col className="submit-btn text-align-center">
                                                <Button variant="dark" type="submit">SUBMIT</Button>
                                            </Col>
                                        </Row>
                                    </Form>
                                </div>
                            </> :
                            <></>}

                        {selectedNavigation == "referralReward" ?
                            <>
                                <div className="page-heading">
                                    <h2>ReferralReward</h2>
                                    <span> Note: Make sure the username is correct</span>
                                </div>
                                <Col style={{ padding: "20px", backgroundColor: "#e8e8e8", borderRadius: "10px" }}>
                                    Your Referral Token Count: {myReferralTokenCount}<br /><br />
                                    {myReferralTokenCount > 0 ?
                                        <Button onClick={() => claimReferralTokens()}>Claim Referral Tokens</Button>
                                        : <></>}
                                </Col>
                            </> :
                            <></>}
                    </Row>
                </div> {/* dashboard content closed  */}
            </div>

            <br /><br /><br />

        </> : <Redirect to="/merchant/login"></Redirect>)
}

export default MerchantHomePage;