import React, { useContext, useEffect, useState } from "react";
import { Row, Col, Container, Button, Badge, Table, Tabs, Tab, Form, Spinner } from 'react-bootstrap';
import { toast } from "react-toastify";
import CustomerLogo from '../../components/images/user.png';
import SelectDocumentModal from "../../components/page-components/selectDocumentModal";
import PdfViewer from "../../components/page-components/PdfViewer";
import { useLocation, Redirect, useHistory } from "react-router-dom";
import { LoginContext } from "../LoginContext";
import { encrypt, decrypt } from '../../components/Encryption/file';
import { getMyDocumentsDocument, getDocument } from '../../components/api/ProjectCalls';
import AddNewRecord from "./addnewrecord";


const CustomerHome = (props) => {

    const history = useHistory();
    const [loginData, setLoginData] = useContext(LoginContext);

    const [documentsData, setDocumentsData] = useState([
        //     {
        //     "nameOnDocument": "Mayuresh Sunil Khemnar",
        //     "documentId": "267907363292",
        //     "documentType": "adhar",
        //     "fileName": "This is sample pdf.pdf",
        //     "fileType": "application/pdf",
        //     "fileUrlHash": "document url",
        // }
    ]);
    const [myAllDocumentCount, setMyAllDocumentCount] = useState(0);
    const [showDocumentURL, setShowDocumentURL] = useState("");
    const [selectedNavigation, setSelectedNavigation] = useState("dashboard");
    // Dashbord counts
    const [allPendingRequestCount, setAllPendingRequestCount] = useState(0);
    const [allAccessAllowedCount, setAllAccessAllowedCount] = useState(0);
    // All reuests
    const [allAccessRequestSelectedTab, setAllAccessRequestSelectedTab] = useState("requested")
    const [allRequestData, setAllRequestData] = useState([]);
    // Access reuest data
    const [accessRequestData, setAccessRequestData] = useState({});
    // Access History 
    const [allAccessHistoryData, setAllAccessHistoryData] = useState([]);
    // Show no access model
    const [modalShow, setModalShow] = useState(false);

    useEffect(() => {
        getAllRequests();
        getAccessHistory();
        getMyData();

        // fix-the-react-memory-leak-
        return () => {
            setDocumentsData([]);
        }
    }, []);

    const getMyData = async () => {
        props.setLoading(true);
        // var encryptedUrl = await loginData.KYCContract.methods.getMyData(loginData.username).call({ from: loginData.walletAddress });
        // // console.log(encryptedUrl);
        // if (encryptedUrl) {
        //     let decryptedUrl = decrypt(encryptedUrl);
        //     console.log(decryptedUrl);
        //     let response = await fetch(decryptedUrl);
        //     var objectData = await response.json();
        //     console.log(objectData);
        //     setDocumentsData(objectData);
        // }
        let res = await getMyDocumentsDocument(loginData.accessToken);
        console.log(res);
        if (res.success) {
            setMyAllDocumentCount(res.data.length);
            let arr = res.data.filter(function (ele) { return ele.status == "active" });
            setDocumentsData(arr);
        }
        props.setLoading(false);
    }

    const showDocument = async (encryptedUrl) => {
        // let decryptedUrl = decrypt(encryptedUrl);
        // console.log(decryptedUrl);
        // setShowDocumentURL(decryptedUrl);
        let url = await getDocument(encryptedUrl);
        setShowDocumentURL(url);
    }

    const getAllRequests = async () => {
        props.setLoading(true);
        var length = await loginData.KYCContract.methods.getAllMyCustomersRequestsLength(loginData.username).call({ from: loginData.walletAddress });
        // console.log(length);
        var allRequests = [];
        var pendingRequestCount = 0;
        var accessAllowedCount = 0;
        for (let i = 0; i < length; i++) {
            var MerchantName = await loginData.KYCContract.methods.getAllMyCustomersRequestsData(loginData.username, i).call({ from: loginData.walletAddress });
            console.log(MerchantName);
            var result = await loginData.KYCContract.methods.getAllRequestsData(MerchantName, loginData.username).call({ from: loginData.walletAddress });
            console.log(result);
            if (result.status == 1)
                pendingRequestCount++;
            else if (result.status == 2)
                if (parseInt(new Date().getTime() / 1000) < result.closeTime)
                    accessAllowedCount++;
            var obj = {
                "merchantName": MerchantName,
                "info": result.info,
                "status": result.status,
                "timestamp": result.timestamp,
                "openTime": result.openTime,
                "closeTime": result.closeTime,
                "documentsAllowed": result.documentsAllowed
            }
            allRequests.push(obj);
        }
        setAllPendingRequestCount(pendingRequestCount);
        setAllAccessAllowedCount(accessAllowedCount);
        setAllRequestData(allRequests);
        props.setLoading(false);
    }

    const getAccessHistory = async () => {
        props.setLoading(true);
        var length = await loginData.KYCContract.methods.getAllMyAccessHistoryLength(loginData.username).call({ from: loginData.walletAddress });
        // console.log(length);
        var allAccessHistory = [];
        for (let i = 0; i < length; i++) {
            var result = await loginData.KYCContract.methods.getMyAccessHistoryData(loginData.username, i).call({ from: loginData.walletAddress });
            console.log(result);
            var obj = {
                "merchantName": result.fromUsername,
                "info": result.info,
                "fromTimestamp": result.fromTimestamp,
                "toTimestamp": result.toTimestamp
            }
            allAccessHistory.push(obj);
        }
        setAllAccessHistoryData(allAccessHistory);
        console.log(allAccessHistory);
        props.setLoading(false);
    }

    const approveRequest = async (data, documentList, closeTime) => {
        props.setLoading(true);
        let encryptedDocumentList = encrypt(documentList);
        try {
            var txId = await loginData.KYCContract.methods.approveRequest(data.merchantName, loginData.username, encryptedDocumentList, closeTime).send({ from: loginData.walletAddress });
            console.log(txId);
            if (txId) {
                toast.success("Request approved successfully!");
                getAllRequests();
                getAccessHistory();
            } else {
                toast.error("Error while approving Request");
                console.error("Error while approving Request");
            }
        } catch (err) {
            var text = err.message.substr(err.message.indexOf('"message": "') + 12);
            var message = text.substr(0, text.indexOf('"'));
            toast.error(message);
            console.log(err.message);
            // console.log(err);
        }
        props.setLoading(false);
    }

    const rejectRequest = async (data) => {
        props.setLoading(true);
        try {
            var txId = await loginData.KYCContract.methods.rejectRequest(data.merchantName, loginData.username).send({ from: loginData.walletAddress });
            console.log(txId);
            if (txId) {
                getAllRequests();
                getAccessHistory();
                toast.success("Request rejected successfully!");
            } else {
                toast.error("Error while rejecting Request");
                console.error("Error while rejecting Request");
            }
        } catch (err) {
            var text = err.message.substr(err.message.indexOf('"message": "') + 12);
            var message = text.substr(0, text.indexOf('"'));
            toast.error(message);
            console.log(err.message);
        }
        props.setLoading(false);
    }

    const closeRequest = async (data) => {
        props.setLoading(true);
        try {
            var txId = await loginData.KYCContract.methods.closeAccess(data.merchantName, loginData.username).send({ from: loginData.walletAddress });
            console.log(txId);
            if (txId) {
                getAllRequests();
                getAccessHistory();
                toast.success("Access closed successfully!");
            } else {
                toast.error("Error while rejecting Request");
                console.error("Error while rejecting Request");
            }
        } catch (err) {
            var text = err.message.substr(err.message.indexOf('"message": "') + 12);
            var message = text.substr(0, text.indexOf('"'));
            toast.error(message);
            console.log(err.message);
        }
        props.setLoading(false);
    }

    const getDocumentsAllowedString=(s)=>{
        let str="";
        let keysArray=Object.keys(s);
        keysArray.forEach((ele,i)=>{
            str+=ele+" : "+s[ele];
            // str+=i<keysArray.length?"\n":"";
        });
        return str;
    }

    return (
        loginData.isConnected ? <>
        <input type="checkbox" id="side" name="" value="" />  
            <div className="main-data-row page-wrap">
                <>
                    <SelectDocumentModal
                        modalshow={modalShow}
                        onhide={() => setModalShow(false)}
                        documentsdata={documentsData}
                        accessrequestdata={accessRequestData}
                        approverequest={approveRequest}
                    />
                </>

                <div className="side-bar-menu sidebar">
                    <div className="homepage-navigation ">
                        <div className="menu-user">
                         <div className="user-img"><img src={CustomerLogo}/></div>
                         <p>Customer</p>   
                        </div>
                        <ul>
                            <li><Button variant="secondary" size="lg" className={selectedNavigation == "dashboard" ? "active" : ""} onClick={() => setSelectedNavigation("dashboard")}><i className="fa fa-tachometer" aria-hidden="true"></i> Dashboard</Button></li>
                            <li><Button variant="secondary" size="lg" className={selectedNavigation == "mydocuments" ? "active" : ""} onClick={() => setSelectedNavigation("mydocuments")}><i className="fa fa-user" aria-hidden="true"></i> My Documents</Button></li>
                            <li><Button variant="secondary" size="lg" className={selectedNavigation == "allaccessrequest" ? "active" : ""} onClick={() => setSelectedNavigation("allaccessrequest")}><i className="fa fa-key" aria-hidden="true"></i> All Access Request</Button></li>
                            <li><Button variant="secondary" size="lg" className={selectedNavigation == "accesshistory" ? "active" : ""} onClick={() => setSelectedNavigation("accesshistory")}><i className="fa fa-pencil-square-o" aria-hidden="true"></i> Access History</Button></li>
                            <li><Button variant="secondary" size="lg" className={selectedNavigation == "addnewrecord" ? "active" : ""} onClick={() => setSelectedNavigation("addnewrecord")}><i className="fa fa-plus-square-o" aria-hidden="true"></i> Add New Record</Button></li>
                        </ul>
                    </div>
                </div>

                <div className="dashboard-content page-content">
                <label for="side" class="toggle"></label>
                    <Row className="homepage-pageData">
                        {/* dashboard page */}
                        {selectedNavigation == "dashboard" ?
                            <>
                                <div className="page-heading">
                                    <h2> Welcome to KYC!</h2>
                                    <span> An KYC authentication using blockchain.</span>
                                    <br /><br />
                                </div>
                                <Row>
                                    <Row className="dashboard-summary-details">
                                        <Col md={4}>
                                            <Button variant="primary" size="lg" onClick={() => setSelectedNavigation("mydocuments")}>
                                                My Documents <Badge bg="secondary">{documentsData.length}</Badge>
                                                <span className="visually-hidden">My Documents {documentsData.length}</span>
                                            </Button>
                                        </Col>
                                        <Col md={4}>
                                            <Button variant="primary" size="lg" onClick={() => { setSelectedNavigation("allaccessrequest"); setAllAccessRequestSelectedTab("requested"); }}>
                                                Pending Requests <Badge bg="secondary">{allPendingRequestCount}</Badge>
                                                <span className="visually-hidden">Pending Requests {allPendingRequestCount}</span>
                                            </Button>
                                        </Col>
                                        <Col md={4}>
                                            <Button variant="primary" size="lg" onClick={() => { setSelectedNavigation("allaccessrequest"); setAllAccessRequestSelectedTab("approved"); }}>
                                                Access Allowed <Badge bg="secondary">{allAccessAllowedCount}</Badge>
                                                <span className="visually-hidden">Allowed Requests {allAccessAllowedCount}</span>
                                            </Button>
                                        </Col>
                                    </Row>
                                </Row>
                            </> :
                            <></>}

                        {/*  mydocuments page */}
                        {selectedNavigation == "mydocuments" ?
                            <>
                                <div className="page-heading">
                                    <h2> My Documents</h2>
                                    <span> All your uploaded doucments</span>
                                </div>
                                <Row>
                                    {documentsData.length > 0 ?
                                        <Table striped bordered hover variant="light">
                                            <thead>
                                                <tr>
                                                    <th>#</th>
                                                    <th>Document Type</th>
                                                    <th>Name on Document</th>
                                                    <th>Document ID</th>
                                                    {/* <th>Address</th> */}
                                                    <th>File Url Hash</th>
                                                    <th>Actions</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {documentsData.map((data, key) => {
                                                    return (
                                                        <tr key={key}>
                                                            <td>{key + 1}</td>
                                                            <td>{data.documentType} {data.isVerified == "true" ? <i class="fa fa-check-circle" aria-hidden="true" style={{ color: "blue" }}></i> : <></>}</td>
                                                            <td style={{ wordBreak: "break-all" }}>{data.nameOnDocument}</td>
                                                            <td>{data.documentId}</td>
                                                            {/* <td>{data.address}</td> */}
                                                            <td style={{ wordBreak: "break-all" }}>{encrypt(data.fileUrl)}</td>
                                                            <td>
                                                                <Button onClick={() => showDocument(data.fileUrl)}>Show</Button>
                                                            </td>
                                                        </tr>
                                                    );
                                                })}
                                            </tbody>
                                        </Table>
                                        : <span className="color-red">No data found</span>}
                                </Row>
                                {showDocumentURL || showDocumentURL != "" ?
                                    <Row>
                                        <Button variant="dark" onClick={() => setShowDocumentURL("")}>Close View</Button>
                                        {/* <PdfViewer pdf={showDocumentURL} 
                                                setShowDocumentURL={setShowDocumentURL}/> */}
                                        <embed src={showDocumentURL} width="100%" height="500px" />
                                    </Row>
                                    : <></>
                                }
                            </> :
                            <></>}
                        {/*  allaccessrequest page */}
                        {selectedNavigation == "allaccessrequest" ?
                            <>
                                <div className="page-heading">
                                    <h2> All Access Request</h2>
                                    {/* <span> All Access Requests.</span> */}
                                </div>
                                <Tabs id="AllAccessRequestTab" className="mb-3"
                                    activeKey={allAccessRequestSelectedTab}
                                    onSelect={(k) => setAllAccessRequestSelectedTab(k)} >
                                    <Tab eventKey="requested" title="Requested">
                                        <Row>
                                            <Table striped bordered hover variant="light">
                                                <thead>
                                                    <tr>
                                                        <th>#</th>
                                                        <th>Merchant Name</th>
                                                        <th>Info</th>
                                                        <th>Documents Allowed</th>
                                                        <th>Status</th>
                                                        <th>Request Send On</th>
                                                        <th>Actions</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {allRequestData.map((data, key) => {
                                                        return (
                                                            data.status == 1 ?
                                                                <tr key={key}>
                                                                    <td>{key + 1}</td>
                                                                    <td>{data.merchantName}</td>
                                                                    <td>{data.info}</td>
                                                                    <td>{getDocumentsAllowedString(JSON.parse(data.documentsAllowed))}</td>
                                                                    <td>Requested</td>
                                                                    <td>{new Date(parseInt(data.timestamp * 1000)).toLocaleString()}</td>
                                                                    <td>
                                                                        <Button onClick={() => { setAccessRequestData(data); setModalShow(true); }}> Approve </Button>
                                                                        <Button variant="danger" onClick={() => rejectRequest(data)}> Reject </Button>
                                                                    </td>
                                                                </tr>
                                                                : <></>
                                                        );
                                                    })}
                                                </tbody>
                                            </Table>
                                        </Row>
                                    </Tab>
                                    <Tab eventKey="approved" title="Approved">
                                        <Row>
                                            <Table striped bordered hover variant="light">
                                                <thead>
                                                    <tr>
                                                        <th>#</th>
                                                        <th>Merchant Name</th>
                                                        <th>Info</th>
                                                        <th>Status</th>
                                                        <th>Request Send Timestamp</th>
                                                        <th>Access Open Timestamp</th>
                                                        <th>Access Close Timestamp</th>
                                                        <th>Actions</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {allRequestData.map((data, key) => {
                                                        return (
                                                            data.status == 2 && parseInt(new Date().getTime() / 1000) < data.closeTime ?
                                                                <tr key={key}>
                                                                    <td>{key + 1}</td>
                                                                    <td>{data.merchantName}</td>
                                                                    <td>{data.info}</td>
                                                                    <td>Approved</td>
                                                                    <td>{new Date(parseInt(data.timestamp * 1000)).toLocaleString()}</td>
                                                                    <td>{data.openTime == 0 ? "-" : new Date(parseInt(data.openTime * 1000)).toLocaleString()}</td>
                                                                    <td>{data.closeTime == 0 ? "-" : new Date(parseInt(data.closeTime * 1000)).toLocaleString()}</td>
                                                                    <td>
                                                                        {parseInt(new Date().getTime() / 1000) < data.closeTime ?
                                                                            <Button variant="danger" onClick={() => closeRequest(data)}> Close Access Now </Button>
                                                                            : <></>}
                                                                    </td>
                                                                </tr>
                                                                : <></>
                                                        );
                                                    })}
                                                </tbody>
                                            </Table>

                                        </Row>
                                    </Tab>
                                    <Tab eventKey="rejected" title="Rejected">
                                        <Row>
                                            <Table striped bordered hover variant="light">
                                                <thead>
                                                    <tr>
                                                        <th>#</th>
                                                        <th>Merchant Name</th>
                                                        <th>Info</th>
                                                        <th>Status</th>
                                                        <th>Request Send Timestamp</th>
                                                        <th>Access Open Timestamp</th>
                                                        <th>Access Close Timestamp</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {allRequestData.map((data, key) => {
                                                        return (
                                                            data.status == -1 ?
                                                                <tr key={key}>
                                                                    <td>{key + 1}</td>
                                                                    <td>{data.merchantName}</td>
                                                                    <td>{data.info}</td>
                                                                    <td>Rejected</td>
                                                                    <td>{new Date(parseInt(data.timestamp * 1000)).toLocaleString()}</td>
                                                                    <td>{data.openTime == 0 ? "-" : new Date(parseInt(data.openTime * 1000)).toLocaleString()}</td>
                                                                    <td>{data.closeTime == 0 ? "-" : new Date(parseInt(data.closeTime * 1000)).toLocaleString()}</td>
                                                                </tr>
                                                                : <></>
                                                        );
                                                    })}
                                                </tbody>
                                            </Table>

                                        </Row>
                                    </Tab>
                                    <Tab eventKey="closed" title="Closed">
                                        <Row>
                                            <Table striped bordered hover variant="light">
                                                <thead>
                                                    <tr>
                                                        <th>#</th>
                                                        <th>Merchant Name</th>
                                                        <th>Info</th>
                                                        <th>Status</th>
                                                        <th>Request Send Timestamp</th>
                                                        <th>Access Open Timestamp</th>
                                                        <th>Access Close Timestamp</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {allRequestData.map((data, key) => {
                                                        return (
                                                            data.status == 3 || parseInt(new Date().getTime() / 1000) > data.closeTime ?
                                                                <tr key={key}>
                                                                    <td>{key + 1}</td>
                                                                    <td>{data.merchantName}</td>
                                                                    <td>{data.info}</td>
                                                                    <td>Closed</td>
                                                                    <td>{new Date(parseInt(data.timestamp * 1000)).toLocaleString()}</td>
                                                                    <td>{data.openTime == 0 ? "-" : new Date(parseInt(data.openTime * 1000)).toLocaleString()}</td>
                                                                    <td>{data.closeTime == 0 ? "-" : new Date(parseInt(data.closeTime * 1000)).toLocaleString()}</td>
                                                                </tr>
                                                                : <></>
                                                        );
                                                    })}
                                                </tbody>
                                            </Table>

                                        </Row>
                                    </Tab>
                                    <Tab eventKey="all" title="All">
                                        <Row>
                                            <Table striped bordered hover variant="light">
                                                <thead>
                                                    <tr>
                                                        <th>#</th>
                                                        <th>Merchant Name</th>
                                                        <th>Info</th>
                                                        <th>Status</th>
                                                        <th>Request Send Timestamp</th>
                                                        <th>Access Open Timestamp</th>
                                                        <th>Access Close Timestamp</th>
                                                        <th>Actions</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {allRequestData.map((data, key) => {
                                                        return (
                                                            <tr key={key}>
                                                                <td>{key + 1}</td>
                                                                <td>{data.merchantName}</td>
                                                                <td>{data.info}</td>
                                                                <td>{data.status == 1 ? "Requested" :
                                                                    data.status == 2 ? "Approved" :
                                                                        data.status == -1 ? "Rejected" : "Closed"}</td>
                                                                <td>{new Date(parseInt(data.timestamp * 1000)).toLocaleString()}</td>
                                                                <td>{data.openTime == 0 ? "-" : new Date(parseInt(data.openTime * 1000)).toLocaleString()}</td>
                                                                <td>{data.closeTime == 0 ? "-" : new Date(parseInt(data.closeTime * 1000)).toLocaleString()}</td>
                                                                <td>
                                                                    {data.status == 1 ?
                                                                        <>
                                                                            <Button onClick={() => { setAccessRequestData(data); setModalShow(true); }}> Approve </Button>
                                                                            <Button variant="danger" onClick={() => rejectRequest(data)}> Reject </Button>
                                                                        </> :
                                                                        data.status == 2 && parseInt(new Date().getTime() / 1000) < data.closeTime ?
                                                                            <Button variant="danger" onClick={() => closeRequest(data)}> Close Access Now </Button>
                                                                            : <></>}
                                                                </td>
                                                            </tr>
                                                        );
                                                    })}
                                                </tbody>
                                            </Table>

                                        </Row>
                                    </Tab>
                                </Tabs>

                            </> :
                            <></>}
                        {/*  accesshistory page */}
                        {selectedNavigation == "accesshistory" ?
                            <>
                                <div className="page-heading">
                                    <h2> Access History</h2>
                                    <span> All details of merchants who has access to your document with access time duration.</span>
                                    <br /><br />
                                </div>
                                <Row>
                                    {allAccessHistoryData.length > 0 ?
                                        <Table striped bordered hover variant="light">
                                            <thead>
                                                <tr>
                                                    <th>#</th>
                                                    <th>Merchant Name</th>
                                                    <th>Info</th>
                                                    <th>From</th>
                                                    <th>To</th>
                                                    <th>Actions</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {allAccessHistoryData.map((data, key) => {
                                                    return (
                                                        <tr key={key}>
                                                            <td>{key + 1}</td>
                                                            <td>{data.merchantName}</td>
                                                            <td>{data.info}</td>
                                                            <td>{new Date(parseInt(data.fromTimestamp * 1000)).toLocaleString()}</td>
                                                            <td>{data.toTimestamp == 0 ? "-" : new Date(parseInt(data.toTimestamp * 1000)).toLocaleString()}</td>
                                                            <td>
                                                                {data.toTimestamp == 0 && parseInt(new Date().getTime() / 1000) < data.closeTime ?
                                                                    <Button variant="danger" onClick={() => closeRequest(data)}> Close Access Now </Button>
                                                                    : <></>}
                                                            </td>
                                                        </tr>
                                                    );
                                                })}
                                            </tbody>
                                        </Table>
                                        : <span className="color-red">No data found</span>}
                                </Row>
                            </> :
                            <></>}
                        {/*  addnewrecord page */}
                        {selectedNavigation == "addnewrecord" ?
                            <AddNewRecord
                                documentsData={documentsData}
                                setDocumentsData={setDocumentsData}
                                myAllDocumentCount={myAllDocumentCount}
                                setMyAllDocumentCount={setMyAllDocumentCount}
                                setLoading={props.setLoading}
                                loading={props.loading}
                            /> :
                            <></>}
                    </Row>
                </div>
            </div>
        </> : <Redirect to="/"></Redirect>)
}

export default CustomerHome