import { useEffect, useContext, useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Spinner } from 'react-bootstrap';
import './App.css';
import { ToastContainer } from 'react-toastify'; //toster
import 'react-toastify/dist/ReactToastify.css';
import { LoginContext } from "./pages/LoginContext";

import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import Menubar from './components/page-components/navgiationBar';
import Footer from './components/page-components/footer';

import Home from './pages/home';
import CustomerLogin from './pages/customer/login';
import CustomerHome from './pages/customer/home';
import MerchantLogin from './pages/merchant/merchantlogin';
import MerchantHomePage from './pages/merchant/merchanthomepage';
import AdminLogin from './pages/admin/login';
import AdminHomePage from './pages/admin/home';

function App() {

  const [loading, setLoading] = useState(false);
  const [loginData, setLoginData] = useContext(LoginContext);
  const [tokenCount, setTokenCount] = useState(0);


  const updateBalance = async () => {
    if (loginData.INRTContract) {
      var result = await loginData.INRTContract.methods.balanceOf(loginData.walletAddress).call();
      console.log(result);
      if (tokenCount != result)
        setTokenCount(result / 10 ** 6);
    }
  }

  return (
    <Router>
      <div className="App">
        <ToastContainer autoClose={4000} limit={3}></ToastContainer>
        <header className="App-header">
          <Menubar tokenCount={tokenCount} setTokenCount={setTokenCount} updateBalance={updateBalance}/>
        </header>
        <div className={loading ? "pageData loading-div" : "pageData"}>
          <div className={loading ? 'loading-bar-center' : 'hide'}>
            <Spinner animation="border" role="status">
              <span className="visually-hidden">Loading...</span>
            </Spinner>
          </div>
          <Switch>
            <Route exact path="/">
              <Home />
            </Route>
            <Route exact path="/login">
              <CustomerLogin loading={loading} setLoading={setLoading} />
            </Route>
            <Route exact path="/home">
              <CustomerHome loading={loading} setLoading={setLoading} updateBalance={updateBalance}/>
            </Route>
            <Route exact path="/merchant/login">
              <MerchantLogin loading={loading} setLoading={setLoading} />
            </Route>
            <Route exact path="/merchant/home">
              <MerchantHomePage loading={loading} setLoading={setLoading} tokenCount={tokenCount} setTokenCount={setTokenCount} updateBalance={updateBalance} />
            </Route>
            <Route exact path="/admin/login">
              <AdminLogin loading={loading} setLoading={setLoading} />
            </Route>
            <Route exact path="/admin/home">
              <AdminHomePage loading={loading} setLoading={setLoading} tokenCount={tokenCount} setTokenCount={setTokenCount} />
            </Route>
            <Route path="*">
              <div>Page not found</div>
            </Route>
          </Switch>
        </div>
        <div className="footer">
          <Footer />
        </div>
      </div>
    </Router>
  );
}

export default App;
