import React, { useContext, useEffect, useState } from "react";
import { Row, Col, Container, Button, Badge, Table, Tabs, Tab, Form, Spinner } from 'react-bootstrap';
import { toast } from "react-toastify";
import CustomerLogo from '../../components/images/user.png';
import DocTypeModal from "../../components/page-components/doctypeModel";
import { useLocation, Redirect, useHistory } from "react-router-dom";
import { LoginContext } from "../LoginContext";
import { encrypt, decrypt } from '../../components/Encryption/file';
import { getWebsiteData, updateWebsiteData, getUserCount } from '../../components/api/ProjectCalls';


const AdminHome = (props) => {

    const history = useHistory();
    const [loginData, setLoginData] = useContext(LoginContext);

    const [selectedNavigation, setSelectedNavigation] = useState("dashboard");
    const [totalCustomers, setTotalCustomers] = useState(0);
    const [totalMerchant, setTotalMerchant] = useState(0);
    // All Website Variables 
    const [allDocumentTypeList, setAllDocumentTypeList] = useState([]);
    const [editDocumentType, setEditDocumentType] = useState(null);
    const [noOfDocumentAllowed, setNoOfDocumentAllowed] = useState(0);
    const [requestFees, setRequestFees] = useState(0);
    const [referralRewardValue, setReferralRewardValue] = useState(0);
    const [BDLTTokenAddress, setBDLTTokenAddress] = useState("");

    useEffect(() => {
        getBDLTToken();
        getRequestFees();
        getReferralRewardValue();
        getWebsiteVariables();
        getUserCountData();
    }, []);

    const deleteDocTypeStr = (key) => {
        let _allDocumentTypeList = [...allDocumentTypeList];
        _allDocumentTypeList.splice(key, 1);
        setAllDocumentTypeList(_allDocumentTypeList);
        updateWebsiteDataCall();
    }

    const approveRequestByOwner = async (_fromMerchant, _toUsername, status) => {
        props.setLoading(true);
        try {
            var txId = await loginData.KYCContract.methods.approveRequestByOwner(_fromMerchant, _toUsername, status).send({ from: loginData.walletAddress });
            console.log(txId);
            if (txId) {
                toast.success("Request approved successfully!");
            } else {
                toast.error("Error while approving Request");
                console.error("Error while approving Request");
            }
        } catch (err) {
            var text = err.message.substr(err.message.indexOf('"message": "') + 12);
            var message = text.substr(0, text.indexOf('"'));
            toast.error(message);
            console.log(err.message);
            // console.log(err);
        }
        props.setLoading(false);
    }

    const getAccessAllowedByAdmin = async (_fromMerchant, _toUsername) => {
        // props.setLoading(true);
        var status = await loginData.KYCContract.methods.getAccessAllowedByAdmin(_fromMerchant, _toUsername).call({ from: loginData.walletAddress });
        console.log(status);
        return status;
        // props.setLoading(false);
    }

    const getUserCountData = async () => {
        let resCustomer = await getUserCount(2);
        // console.log(resCustomer);
        setTotalCustomers(resCustomer.data);
        let resMerchant = await getUserCount(3);
        // console.log(resMerchant);
        setTotalMerchant(resCustomer.data);
    }

    const getWebsiteVariables = async () => {
        let res = await getWebsiteData();
        console.log(res);
        setAllDocumentTypeList(res.data[0].documentTypeList);
        setNoOfDocumentAllowed(res.data[0].noOfDocumentAllowed);
    }

    const updateWebsiteDataCall = async (_allDocumentTypeList) => {
        let res = await updateWebsiteData(noOfDocumentAllowed, _allDocumentTypeList, loginData.accessToken);
        console.log(res);
    }

    const setNewBDLTToken = async () => {
        props.setLoading(true);
        try {
            var txId = await loginData.KYCContract.methods.setBDLTToken(BDLTTokenAddress).send({ from: loginData.walletAddress });
            console.log(txId);
            if (txId) {
                toast.success("Request approved successfully!");
            } else {
                toast.error("Error while approving Request");
                console.error("Error while approving Request");
            }
        } catch (err) {
            var text = err.message.substr(err.message.indexOf('"message": "') + 12);
            var message = text.substr(0, text.indexOf('"'));
            toast.error(message);
            console.log(err.message);
            // console.log(err);
        }
        props.setLoading(false);
    }

    const getBDLTToken = async () => {
        props.setLoading(true);
        var address = await loginData.KYCContract.methods.getBDLTToken().call({ from: loginData.walletAddress });
        console.log(address);
        setBDLTTokenAddress(address);
        props.setLoading(false);
    }

    const setNewRequestFees = async () => {
        props.setLoading(true);
        try {
            var txId = await loginData.KYCContract.methods.setRequestFees(requestFees).send({ from: loginData.walletAddress });
            console.log(txId);
            if (txId) {
                toast.success("Request approved successfully!");
            } else {
                toast.error("Error while approving Request");
                console.error("Error while approving Request");
            }
        } catch (err) {
            var text = err.message.substr(err.message.indexOf('"message": "') + 12);
            var message = text.substr(0, text.indexOf('"'));
            toast.error(message);
            console.log(err.message);
            // console.log(err);
        }
        props.setLoading(false);
    }

    const getRequestFees = async () => {
        props.setLoading(true);
        var fees = await loginData.KYCContract.methods.getRequestFees().call({ from: loginData.walletAddress });
        console.log(fees);
        setRequestFees(fees);
        props.setLoading(false);
    }

    const setNewReferralRewardValue = async () => {
        props.setLoading(true);
        try {
            var txId = await loginData.KYCContract.methods.setReferralRewardValue(referralRewardValue).send({ from: loginData.walletAddress });
            console.log(txId);
            if (txId) {
                toast.success("Request approved successfully!");
            } else {
                toast.error("Error while approving Request");
                console.error("Error while approving Request");
            }
        } catch (err) {
            var text = err.message.substr(err.message.indexOf('"message": "') + 12);
            var message = text.substr(0, text.indexOf('"'));
            toast.error(message);
            console.log(err.message);
            // console.log(err);
        }
        props.setLoading(false);
    }

    const getReferralRewardValue = async () => {
        props.setLoading(true);
        var value = await loginData.KYCContract.methods.getReferralRewardValue().call({ from: loginData.walletAddress });
        console.log(value);
        setReferralRewardValue(value);
        props.setLoading(false);
    }

    return (
        loginData.isConnected ? <>
            <input type="checkbox" id="side" name="" value="" />
            <div className="main-data-row page-wrap">
                <>
                    <DocTypeModal
                        modalshow={editDocumentType != null}
                        onhide={() => setEditDocumentType(null)}
                        editDocumentType={editDocumentType}
                        allDocumentTypeList={allDocumentTypeList}
                        setAllDocumentTypeList={setAllDocumentTypeList}
                        updateWebsiteDataCall={updateWebsiteDataCall}
                    />
                </>

                <div className="side-bar-menu sidebar">
                    <div className="homepage-navigation ">
                        <div className="menu-user">
                            <div className="user-img"><img src={CustomerLogo} /></div>
                            <p>Admin</p>
                        </div>
                        <ul>
                            <li><Button variant="secondary" size="lg" className={selectedNavigation == "dashboard" ? "active" : ""} onClick={() => setSelectedNavigation("dashboard")}><i className="fa fa-tachometer" aria-hidden="true"></i> Dashboard</Button></li>
                            <li><Button variant="secondary" size="lg" className={selectedNavigation == "websitevariables" ? "active" : ""} onClick={() => setSelectedNavigation("websitevariables")}><i className="fa fa-level-up" aria-hidden="true"></i> Update Website Variables</Button></li>
                            <li><Button variant="secondary" size="lg" className={selectedNavigation == "documenttypelist" ? "active" : ""} onClick={() => setSelectedNavigation("documenttypelist")}><i className="fa fa-list" aria-hidden="true"></i> Update Document Type List</Button></li>
                        </ul>
                    </div>
                </div>

                <div className="dashboard-content page-content">
                    <label for="side" class="toggle"></label>
                    <Row className="homepage-pageData">
                        {/* dashboard page */}
                        {selectedNavigation == "dashboard" ?
                            <>
                                <div className="page-heading">
                                    <h2> Admin Panel</h2>
                                    <br /><br />
                                </div>
                                <Row>
                                    <Row className="dashboard-summary-details">
                                        <Col md={4}>
                                            <Button variant="primary" size="lg">
                                                Total Customers <Badge bg="secondary">{totalCustomers}</Badge>
                                                <span className="visually-hidden">Total Customers {totalCustomers}</span>
                                            </Button>
                                        </Col>
                                        <Col md={4}>
                                            <Button variant="primary" size="lg">
                                                Total Merchants <Badge bg="secondary">{totalMerchant}</Badge>
                                                <span className="visually-hidden">Total Merchants {totalMerchant}</span>
                                            </Button>
                                        </Col>
                                        <Col md={4}>
                                            <Button variant="primary" size="lg">
                                                Total Documents Uploaded <Badge bg="secondary">{0}</Badge>
                                                <span className="visually-hidden">Total Documents Uploaded {0}</span>
                                            </Button>
                                        </Col>
                                    </Row>
                                </Row>
                            </> :
                            <></>}

                        {/*  websitevariables page */}
                        {selectedNavigation == "websitevariables" ?
                            <>
                                <div className="page-heading">
                                    <h2> Update Website Variables</h2>
                                </div>
                                <Form>
                                    <Row>
                                        <Col xs="2">
                                            <Form.Label controlId="nameOnDocument">No Of Document Allowed</Form.Label>
                                        </Col>
                                        <Col xs="6">
                                            <Form.Control type="text" id="nameOnDocument" className="input-design" placeholder="" value={noOfDocumentAllowed} onChange={e => setNoOfDocumentAllowed(e.target.value)} required />
                                        </Col>
                                        <Col xs="auto">
                                            <Button onClick={() => updateWebsiteDataCall(allDocumentTypeList)}>Update</Button>
                                        </Col>
                                    </Row>

                                    <Row>
                                        <Col xs="2">
                                            <Form.Label controlId="nameOnDocument">BDLT Token Address</Form.Label>
                                        </Col>
                                        <Col xs="6">
                                            <Form.Control type="text" id="nameOnDocument" className="input-design" placeholder="" value={BDLTTokenAddress} onChange={e => setBDLTTokenAddress(e.target.value)} required />
                                        </Col>
                                        <Col xs="auto">
                                            <Button onClick={() => setNewBDLTToken()}>Update</Button>
                                        </Col>
                                    </Row>

                                    <Row>
                                        <Col xs="2">
                                            <Form.Label controlId="nameOnDocument">Referral Reward Value</Form.Label>
                                        </Col>
                                        <Col xs="6">
                                            <Form.Control type="text" id="nameOnDocument" className="input-design" placeholder="" value={referralRewardValue} onChange={e => setReferralRewardValue(e.target.value)} required />
                                        </Col>
                                        <Col xs="auto">
                                            <Button onClick={() => setNewReferralRewardValue()}>Update</Button>
                                        </Col>
                                    </Row>

                                    <Row>
                                        <Col xs="2">
                                            <Form.Label controlId="nameOnDocument">Request Fees</Form.Label>
                                        </Col>
                                        <Col xs="6">
                                            <Form.Control type="text" id="nameOnDocument" className="input-design" placeholder="" value={requestFees} onChange={e => setRequestFees(e.target.value)} required />
                                        </Col>
                                        <Col xs="auto">
                                            <Button onClick={() => setNewRequestFees()}>Update</Button>
                                        </Col>
                                    </Row>

                                </Form>
                            </> :
                            <></>}
                        {/*  documenttypelist page */}
                        {selectedNavigation == "documenttypelist" ?
                            <>
                                <div className="page-heading">
                                    <h2> Update Document Type List</h2>
                                    <span> List of all document type for user to select document from.</span>
                                    <br /><br />
                                </div>
                                <Row>
                                    Total Document Types : {allDocumentTypeList.length} <br />
                                    <Button onClick={() => setEditDocumentType([null, -1, false])}><i class="fa fa-plus-square-o" aria-hidden="true"></i></Button>

                                    <Table striped bordered hover variant="light">
                                        <thead>
                                            <tr>
                                                <th>#</th>
                                                <th>Document Type<br />(non-spacing text)</th>
                                                <th>Document Type Text<br />(User view)</th>
                                                <th>Action</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {/* <tr>
                                                    <td>{1}</td>
                                                    <td>{"adhar"} </td>
                                                    <td>
                                                        <Button onClick={() => console.log(0)}>Show</Button>
                                                    </td>
                                                </tr> */}

                                            {allDocumentTypeList.map((data, key) => {
                                                return (
                                                    <tr key={key}>
                                                        <td>{key + 1}</td>
                                                        <td>{data.value}</td>
                                                        <td>{data.text}</td>
                                                        <td>
                                                            <Button onClick={() => setEditDocumentType([data, key, true])}><i className="fa fa-pencil" aria-hidden="true"></i></Button>
                                                            <Button onClick={() => deleteDocTypeStr(key)}><i className="fa fa-trash" aria-hidden="true"></i></Button>
                                                        </td>
                                                    </tr>
                                                );
                                            })}
                                        </tbody>
                                    </Table>
                                </Row>
                            </> :
                            <></>}
                    </Row>
                </div>
            </div>
        </> : <Redirect to="/"></Redirect>)
}

export default AdminHome