import React, { useState, useEffect } from "react";
import { Button, Modal, Row, Col, Table } from "react-bootstrap";
import { toast } from "react-toastify";


function SelectDocumentModal(props) {

  const [durationCount, setDurationCount] = useState("5");
  const [durationType, setDurationType] = useState("minutes");

  useEffect(() => {
    setUpUsingJSON();
  }, [props.modalshow]);

  function isEmpty(obj) {
    return Object.keys(obj).length === 0;
  }

  const setUpUsingJSON = () => {
    if (props.accessrequestdata.documentsAllowed!=undefined) {
      var jsonData = JSON.parse(props.accessrequestdata.documentsAllowed);
      props.documentsdata.forEach(function (element, index) {
        if (jsonData[element.documentType] != null) {
          document.getElementById("select" + index).checked = true;
          if (jsonData[element.documentType] == "View") {
            document.getElementById("view" + index).checked = true;
          } else if (jsonData[element.documentType] == "Download") {
            document.getElementById("download" + index).checked = true;
          }
        }
      });
    }
  }

  const createAccessJSON = () => {
    var jsonData = {};
    props.documentsdata.forEach(function (element, index) {
      var isSelected = document.getElementById("select" + index).checked;
      if (isSelected) {
        if (document.getElementById("view" + index).checked) {
          let _accessType = document.getElementById("view" + index).value;
          jsonData[element.documentType] = _accessType;
        } else if (document.getElementById("download" + index).checked) {
          let _accessType = document.getElementById("download" + index).value;
          jsonData[element.documentType] = _accessType;
        }
      }
    });
    if (isEmpty(jsonData)) {
      toast.error("Please select documents first");
    } else {
      let t = 0;
      if (durationType == "minutes") {
        t = durationCount * 60;
      } else if (durationType == "hours") {
        t = durationCount * 3600;
      } else if (durationType == "days") {
        t = durationCount * 86400;
      }
      var closeTime = parseInt(new Date().getTime() / 1000) + t;
      // console.log(closeTime);
      // console.log(new Date(closeTime*1000).toLocaleString());
      console.log(JSON.stringify(jsonData));
      props.approverequest(props.accessrequestdata, JSON.stringify(jsonData), closeTime);
      props.onhide();
    }
  }

  return (
    <Modal show={props.modalshow} onHide={props.onhide} className="popup-lg">
      <Modal.Body style={{ padding: '0px' }}>
        <div className="popup-box">
          <div className="closed">
            <a style={{ border: 'none' }} onClick={props.onhide}><span style={{ fontSize: '25px' }}></span></a>
          </div>

          <div className="popup-data">
            <div className="popup-header">
              <h3>Select Documents</h3>
            </div>
            <div className="popup-body">
              <Row>
                <p>Select documents to share with proper access type</p>
                <Table striped bordered hover variant="light">
                  <thead>
                    <tr>
                      <th>#</th>
                      <th>Document Type</th>
                      <th>Select</th>
                      <th>Access type</th>
                    </tr>
                  </thead>
                  <tbody>
                    {props.documentsdata.map((data, key) => {
                      return (
                        <tr key={key}>
                          <td>{key + 1}</td>
                          <td>{data?.documentType}{data?.isVerified == "true" ? <i class="fa fa-check-circle" aria-hidden="true" style={{ color: "blue" }}></i> : <></>}</td>
                          <td className="text-align-center">
                            <input type="checkbox" id={"select" + key} name="selected" value="select" />
                          </td>
                          <td>
                            <label><input type="radio" id={"view" + key} name={"accessType" + key} value="View" defaultChecked />
                              View</label> &ensp;
                            <label><input type="radio" id={"download" + key} name={"accessType" + key} value="Download" />
                              Download</label>
                          </td>
                        </tr>
                      );
                    })}
                  </tbody>
                </Table>
                <Row className="text-align-center select-duration">
                  <Col md={12}>
                    <select name="durationCount" id="durationCount" defaultValue={durationCount}
                      onChange={(event) => setDurationCount(event.target.value)}>
                      {durationType == "minutes" ? <></> : <>
                        <option value="1">1</option>
                        <option value="2">2</option>
                        <option value="3">3</option>
                        <option value="4">4</option>
                      </>}
                      <option value="5">5</option>
                      <option value="6">6</option>
                      <option value="7">7</option>
                      <option value="8">8</option>
                      <option value="9">9</option>
                      <option value="10">10</option>
                      <option value="11">11</option>
                      <option value="12">12</option>
                      <option value="13">13</option>
                      <option value="14">14</option>
                      <option value="15">15</option>
                      <option value="16">16</option>
                      <option value="17">17</option>
                      <option value="18">18</option>
                      <option value="19">19</option>
                      <option value="20">20</option>
                      <option value="21">21</option>
                      <option value="22">22</option>
                      <option value="23">23</option>
                      <option value="24">24</option>
                      <option value="25">25</option>
                      <option value="26">26</option>
                      <option value="27">27</option>
                      <option value="28">28</option>
                      <option value="29">29</option>
                      <option value="30">30</option>
                    </select>

                    <select name="durationType" id="durationType" defaultValue={durationType} onChange={(event) => setDurationType(event.target.value)}>
                      <option value="minutes">Minutes</option>
                      <option value="hours">Hours</option>
                      <option value="days">Days</option>
                    </select>
                  </Col>
                </Row>
                <Button onClick={() => createAccessJSON()}>Submit</Button>
              </Row>
            </div>
            <div className="footer-popup">
            </div>
          </div>

        </div>
      </Modal.Body>
    </Modal>
  );
}

export default SelectDocumentModal