var CryptoJS = require("crypto-js");

var secretPassphrase="U2FsdGVkX18ZUVvShFSES21qHsQEqZXMxQ9zgHy+bu0=";

export function encrypt(message){
    var encrypted = CryptoJS.AES.encrypt(message, secretPassphrase).toString();
    return encrypted;
}

export function decrypt(encrypted){
    var bytes = CryptoJS.AES.decrypt(encrypted, secretPassphrase);
    var decrypted = bytes.toString(CryptoJS.enc.Utf8);
    return decrypted;
}

export function hash(string){
    var hash = CryptoJS.SHA256(string);
    return hash.toString(CryptoJS.enc.Base64);
}
