import Web3 from 'web3';
import React, { useEffect, useState, useContext } from "react";
import { Button, Form, Container, Row, Col } from "react-bootstrap";
import { toast } from 'react-toastify'; //toster
import { address, abi } from '../components/contracts/ContractData';
import User from "../components/images/user.png";
import Merchants from "../components/images/merchant.png";
import '../components/css/style.css';

// Route to contribute page 
import { useHistory } from "react-router-dom";

function Home(props) {

  // jump to another page
  const history = useHistory()


  return (<>

    <Container>


      <div className="home-heading">
        <h2> Who are you? </h2>
      </div>

      <div className='user-row-ht'>
        <div className="user-row">
          <div className='user-box'>
            <img src={Merchants} />
            <div className='btn-row'>
              <Button onClick={() => history.push('/merchant/login')}> Merchants </Button>
            </div>
          </div>

          <div className='user-box'>
            <img src={User} />
            <div className='btn-row'>
              <Button onClick={() => history.push('/login')}> Users </Button>
            </div>
          </div>
        </div>
      </div>




    </Container>
  </>
  )
}
export default Home


