import Web3 from 'web3';
import React, { useEffect, useState, useContext } from "react";
import { Button, Form, Container, Row, Col } from "react-bootstrap";
import { toast } from 'react-toastify'; //toster
import { address, abi } from '../../components/contracts/ContractData';
import { tokenAddress, tokenABI } from '../../components/contracts/INRTTokenContractData';
import { createCustomer, userLogin } from '../../components/api/ProjectCalls';
import { hash } from '../../components/Encryption/file';

// Route to contribute page 
import { useHistory } from "react-router-dom";
import { LoginContext } from '../LoginContext';

function AdminLogin(props) {
  const [loginData, setLoginData] = useContext(LoginContext);
  const [username, setUsername] = useState('');

  // jump to another page
  const history = useHistory()

  useEffect(() => {

    if (localStorage.getItem('rememberMe')) {
      const username = localStorage.getItem('admin_username')
      setUsername(username)
    }
    if (window.ethereum) {
      window.ethereum.on('chainChanged', () => {
        window.location.reload();
      })
      window.ethereum.on('accountsChanged', function (accounts) {
        // Time to reload your interface with accounts[0]!
        connect();
      });
      connect();
    }
    // fix-the-react-memory-leak-
    return () => {
      props.setLoading(false);
    }
  }, [])


  async function connect() {
    let web3;
    let KYCContract, INRTContract;
    //connect to web3
    if (typeof window !== 'undefined' && typeof window.web3 !== 'undefined') {
      //we are in the browser and metamask is running
      //ETH test net 3,  // mainnet 1
      // BSC test net 97  //mainnet 56
      web3 = new Web3(window.web3.currentProvider);
      let netId = await web3.eth.net.getId();
      var networkName = "";
      console.log(netId);
      switch (netId) {
        case 97:
          networkName = 'BSC'
          console.log('Network is BSC mainnet')
          break
        default:
          toast.error('Please select mainnet first')
          console.error('Please select mainnet first')
          return;
      }
      //set details
      //log network
      web3.eth.net.getNetworkType().then(console.log);
      let accounts;
      try {
        accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
      } catch (error) {
        if (error.code === 4001) {
          console.error("Please allow metamask to connect")
        }
        return;
        // console.log(error)
      }
      console.log(accounts)
      if (accounts.length < 1) {
        console.error('Please unlock metamask wallet first!')
        return;
      }
      console.log(accounts[0]);

      KYCContract = new web3.eth.Contract(abi, address);
      INRTContract = new web3.eth.Contract(tokenABI, tokenAddress);
      setLoginData({
        walletAddress: accounts[0],
        isConnected: true,
        username: "",
        web3: web3,
        role: null,
        KYCContract: KYCContract,
        INRTContract: INRTContract
      });

    } else {
      //on the browser or user is not running metamask
      console.error("Metamask wallet not found! Please make sure wallet is installed and running!");
    }
  }

  const handleSubmit = async (event) => {
    event.preventDefault();
    props.setLoading(true);
    const user = {
      username: event.target.username.value,
      password: event.target.password.value
    }
    localStorage.setItem('rememberMe', event.target.rememberMe.checked)
    localStorage.setItem('admin_username', username)

    if (loginData.KYCContract == null) {
      connect();
      // toast.error("Please connect metamask first");
      // props.setLoading(false);
      // return;
    } else{
      var address = await loginData.KYCContract.methods.owner().call();
      console.log(address.toLowerCase());
      if(loginData.walletAddress!=address.toLowerCase()){
        toast.error("Admin wallet Address is incorrect");
        props.setLoading(false);
        return;
      }
    }
    try {
      let res = await userLogin(user.username, user.password);
      console.log(res);
      if (res.success) {
        if (res.response.user[0].role == "1") {
          setLoginData({ ...loginData, ...{ username: user.username, role: res.response.user.role, accessToken: res.response.accessToken } });
          //navigate to admin page
          history.push('/admin/home');
        } else
          toast.error("You are not admin");
      } else
        toast.error("Error while login call");
    } catch (err) {
      toast.error(err.message);
      console.log(err.message);
    }
    props.setLoading(false);
  }

  const handleUsername = (event) => {
    setUsername(event.target.value);
  }

  return (<>

    <Container>
      <div className='login-page'>
        <div className="home-heading">
          <h2> Login</h2>
        </div>

        <Form onSubmit={handleSubmit}>
          <Form.Group className="mb-3 input-form" controlId="formUsername">
            <Form.Label>Username</Form.Label>
            <Form.Control className="input-design" type="username" id="username" placeholder="Enter Username" value={username} onChange={handleUsername} required />
          </Form.Group>

          <Form.Group className="mb-3 input-form" controlId="formPassword">
            <Form.Label>Password</Form.Label>
            <Form.Control className="input-design" type="password" id="password" placeholder="Password" required />
          </Form.Group>

          <Form.Group className="mb-3" controlId="formRememberMe">
            <Form.Check type="checkbox" id="rememberMe" label="Remember me " />
          </Form.Group>

          <div className='btn-row'>
            <Button variant="primary" type="submit" disabled={props.loading}>
              Login
            </Button>
          </div>
        </Form>


      </div>
    </Container>
  </>
  )
}
export default AdminLogin


